#dragPalette, #gradientPalette{
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left: 0;
    touch-action: none;
    border-radius: 20px;
    touch-action: none;
}

.dragItem {
    z-index: 1;
    touch-action: none;
    width: 20px;
    height: 20px;
    border: 3px solid #ffffff;
    border-radius: 50%;
    touch-action: none;
    user-select: none;
    transition: all 0.2s cubic-bezier(.39,.58,.57,1);
}



@media (hover: hover) and (pointer: fine) {
    .dragItem:hover, .dragItem:active, .dragItem.active{
        cursor: pointer;
        border-width: 8px;
    }

}

@media (hover: none) and (pointer: coarse) {
    .dragItem:active, .dragItem:focus, .dragItem.active{
        border-width: 8px;
    }
}

.dragIContainer{    
    position: absolute;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
}

.dragIContainer .sketch-picker {
    transform: translate(calc(-50% + 18px), 2px);
}